import { mapState } from 'vuex'
export default {
    name: 'businessMainPage',    
    computed: {
        ...mapState(['userInfo'])
    },
    data() {
        return {
            showWaiting:false,
            loopCount: 0
        }
    },
    destroyed() {
        this.$http.abort()
    },
    methods: {
        startScan () {
            let that = this
            uni.scanCode({
                onlyFromCamera: true,
                scanType: 'QR_CODE',
                success (res) {
                    that.postData(res.result)
                },
                fail () {
                    that.$tip.toast('扫描失败,请重试','none')
                }
            })
        },
        async postData (val) {
            let temp = JSON.parse(val)
            // let now = new Date().getTime()
            // if (now - temp.t > 120000) {
            //     this.$tip.alertDialog('该二维码已超过两分钟有效时间，请提示用户刷新二维码后重试')
            //     return 
            // }
            let params = {
                ...temp
            }
            const res = await this.$api.venueOrder(params)
            if (res.code === 0) {   
                this.showWaiting = true
                this.adminLoopFn(res.data.id)
            } else {
                this.$tip.alertDialog('扫描失败，请重试','知道了')
            }
        },
        async adminLoopFn (id) {
            this.loopCount++
            if (this.loopCount >= 12) {     
                this.showWaiting = false    
                this.loopCount = 0       
                this.$tip.toast('用户支付后，请在统计页查看','none')
                return
            }
            let params = {
                order_id: id
            }
            const res = await this.$api.adminLoopOrder(params)
            if (res.code === 0) {
                this.$tip.alertDialog('用户支付成功')
                this.showWaiting = false
            } else if (res.code === 100) {
                this.adminLoopFn(id)
            } else {                
                this.showWaiting = false
                this.$tip.toast(res.msg,'none')
            } 
        },
        closeLoading () {
            this.showWaiting = false
            this.$http.abort()
            this.loopCount = 0
        }
    },
}
